  font-family: Playfair Display;
  src: url("../fonts/playfairdisplay.woff2") format("woff2"), url("../fonts/playfairdisplay.woff") format("woff");
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: Playfair Display;
  src: url("../fonts/playfairdisplay_italic.woff2") format("woff2"), url("../fonts/playfairdisplay_italic.woff") format("woff");
