body {
  background: #FFF;
  line-height: 1.4;
  padding: 0;
  margin: 0;
  font-family: Arimo;
}

main {
  padding: 0 20px;
}

.container {
  margin: 0 auto;
  max-width: 1366px;
  width: 100%;
  padding: 0 15px;
}

.feed {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  justify-content: flex-start;
}

.feed__main {
  width: 100%;
  margin-right: 30px;
}

.feed__sidebar {
  max-width: 310px;
  width: 100%;
}

.logo {
  margin: 0 auto;
  text-align: center;
}

.logo__text {
  font-size: 0;
  margin: 0;
  padding: 0;
}

.logo__img {
  margin: 10px auto;
}

.intro {
  margin: 0 auto;
  max-width: 1670px;
  width: 100%;
  box-sizing: border-box;
  border: 10px solid #fefae9;
  border-bottom: none;
  border-right: none;
  position: relative;
  padding: 75px 20px;
  margin-bottom: 80px;
}

.intro::after, .intro::before {
  content: '';
  background-color: #fefae9;
  display: block;
  position: absolute;
}

.intro::after {
  top: 0;
  right: 0;
  width: 10px;
  height: 35%;
}

.intro::before {
  bottom: 0;
  left: 0;
  height: 10px;
  width: 55%;
}

.intro__wrapper {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  max-width: 1336px;
}

.intro__content {
  padding-top: 140px;
  z-index: 5;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

.intro__head {
  font-size: 56px;
  line-height: 1.3;
  font-family: Playfair Display;
  letter-spacing: 3px;
}

.intro__head:hover {
  color: #ff7864;
  transition: color .2s ease-in;
}

.intro__filter {
  background-color: #fdefeb;
  padding: 5px;
}

.intro__img {
  max-width: 653px;
  width: 100%;
  position: relative;
  margin-left: -250px;
}

.intro__img::after {
  content: '';
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: #d8eaf6;
  opacity: .9;
  z-index: 2;
}

.top {
  font-family: Playfair Display;
  font-style: italic;
  font-size: 32px;
  color: #4b5860;
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
}

.top__icon {
  width: 35px;
  height: 24px;
  margin-right: 15px;
}

.top__content {
  max-width: 520px;
}

.top__author {
  font-family: Montserrat;
  font-weight: 500;
  font-style: normal;
  color: #e69881;
  text-transform: uppercase;
  font-size: 12px;
  line-height: 2;
  letter-spacing: 3px;
}

.image {
  display: block;
  width: 100%;
  height: auto;
}

.extra {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  text-transform: uppercase;
  font-size: 10px;
  font-family: Montserrat;
  font-weight: 500;
  color: #4b5860;
  margin-top: auto;
  word-spacing: 3px;
  vertical-align: top;
  position: relative;
}

.extra__item {
  margin-right: 10px;
  text-decoration: none;
  color: inherit;
}

.extra__item::after {
  content: '|';
  margin-left: 10px;
  color: #4b5860;
}

.extra__item:last-child::after {
  content: '';
  margin: 0;
}

.extra__link {
  text-decoration: none;
  color: inherit;
}

.extra__link:hover {
  text-decoration: underline;
  color: #ff7864;
  transition: color .3s ease-in;
}

.extra__img {
  display: inline-block;
  width: 13px;
  height: 12px;
  position: absolute;
  top: 2px;
  margin-right: 25px;
}

.extra__text {
  margin-left: 20px;
}

.posts {
  padding: 20px;
  padding-left: 0;
  margin-bottom: 50px;
}

.post {
  width: 100%;
  color: #4b5860;
  display: flex;
  flex-direction: row;
  align-items: center;
}

.post__img {
  min-width: 290px;
  margin-right: -40px;
}

.post__title {
  margin-bottom: 30px;
  padding: 0;
  font-family: Playfair Display, sans serif;
  font-weight: normal;
  font-size: 46px;
  letter-spacing: 0.003em;
  text-decoration: none;
  color: inherit;
}

.post__title:hover {
  color: #ff7864;
}

.post__preview {
  font-weight: normal;
  font-size: 16px;
  letter-spacing: 0em;
}

.post .extra {
  margin-bottom: 30px;
}

.quote {
  border: 10px solid #fefae9;
  border-top: none;
  border-right: none;
  position: relative;
  padding: 90px 40px 40px 100px;
}

.quote::before, .quote::after {
  content: '';
  background-color: #fefae9;
  display: block;
  position: absolute;
}

.quote::before {
  top: 0;
  left: 0;
  height: 10px;
  width: 55%;
}

.quote::after {
  bottom: 0;
  right: 0;
  width: 10px;
  height: 45%;
}

.quote__title {
  font-family: Playfair Display, sans serif;
  font-weight: normal;
  font-size: 56px;
  letter-spacing: 0.003em;
}

.quote__wrapper {
  font-family: Playfair Display;
  font-style: italic;
  font-size: 32px;
  color: #4b5860;
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  margin-bottom: 50px;
}

.quote__icon {
  width: 35px;
  height: 24px;
  margin-right: 15px;
}

.quote__content {
  max-width: 700px;
}

.quote__author {
  font-family: Montserrat;
  font-weight: 500;
  font-style: normal;
  color: #e69881;
  text-transform: uppercase;
  font-size: 12px;
  line-height: 2;
  letter-spacing: 3px;
}

.sidebar {
  background-color: red;
}

.news {
  width: 100%;
  display: flex;
  flex-direction: row;
  color: #4b5860;
  margin-bottom: 50px;
}

.news:last-child {
  margin-bottom: 0;
}

.news__poster {
  display: block;
  min-width: 85px;
  height: auto;
  margin-right: 15px;
}

.news__title {
  font-family: Playfair Display, sans serif;
  font-weight: normal;
  font-size: 18px;
  letter-spacing: 0em;
  color: inherit;
  margin-bottom: 10px;
}

.news__title:hover {
  color: #ff7864;
}

.news__info {
  text-transform: uppercase;
  font-size: 10px;
  color: inherit;
}

.news__item {
  margin-right: 10px;
  text-decoration: none;
  color: inherit;
  position: relative;
}

.news__item::after {
  content: '|';
  margin-left: 10px;
  color: #4b5860;
}

.news__item:last-child::after {
  content: '';
  margin: 0;
}

.news__link {
  text-decoration: none;
  color: inherit;
}

.news__link:hover {
  text-decoration: underline;
  color: #ff7864;
  transition: color .3s ease-in;
}

.news__img {
  display: inline-block;
  width: 13px;
  height: 12px;
  position: absolute;
  top: 2px;
  margin-right: 25px;
}

.news__text {
  padding-left: 20px;
}

.pagination {
  margin: 75px auto;
  text-align: center;
  color: #4b5860;
  font-size: 16px;
}

.pagination__item {
  color: inherit;
  text-decoration: none;
  margin-right: 20px;
  padding: 3px;
}

.pagination__item:hover {
  color: #ff7864;
}

.pagination__item::last-child {
  margin-right: 0;
}

.pagination__item--isactive {
  border-bottom: 2px solid #ff7864;
}

.social {
  width: 100%;
  height: 220px;
  border: 5px solid;
  border-bottom: none;
  text-align: center;
  padding-top: 30px;
  position: relative;
  margin-bottom: 30px;
}

.social::before, .social::after {
  content: '';
  display: block;
  height: 5px;
  width: 85px;
  position: absolute;
  bottom: 0;
}

.social::before {
  left: 0;
}

.social::after {
  right: 0;
}

.social--facebook {
  border-color: #d8eaf6;
}

.social--facebook::before, .social--facebook::after {
  background-color: #d8eaf6;
}

.social--youtube {
  border-color: #fdefeb;
}

.social--youtube::before, .social--youtube::after {
  background-color: #fdefeb;
}

.social__img {
  width: 140px;
  height: 120px;
  margin-bottom: 15px;
}

.social__title {
  text-transform: uppercase;
  font-family: Montserrat;
  font-weight: 700;
  font-size: 16px;
  color: #4b5860;
  margin-bottom: 8px;
}

.social__subscribe {
  color: #ff7864;
  text-decoration: none;
  font-size: 12px;
  text-transform: uppercase;
}

.social__subscribe:hover {
  font-weight: 700;
}

.commercial {
  width: 100%;
  height: 250px;
  box-sizing: border-box;
  border: 1px solid #f4f4f4;
  display: flex;
  justify-content: space-around;
  align-items: center;
}

.commercial__big {
  height: 605px;
}

.commercial__text {
  color: #f4f4f4;
}

.block {
  display: flex;
}

.footer {
  background-color: #f4f9fd;
}

.footer__list {
  display: inline-block;
}
